function createData(OosForward, ShieldsLagframes, Move, OosBack) {
    return {
        'OosForward':OosForward,
        'ShieldsLagframes':ShieldsLagframes,
        'Move': Move,
        'OosBack': OosBack
        };
}
const rows =[
    createData('', 0, '',''),
];

function _setOosForward(frame, OosForward){
    if(frame === '' || frame === '-'){
        return;
    }
    if(frame in rows){
        rows[frame].OosForward += OosForward + " ";
    }else{
        if(frame <= 0 ){
            rows[0].OosForward += OosForward + " ";
        }
        if(frame >= rows.length){
            rows[rows.length-1].OosForward += OosForward + " ";
        }
    }
}
function _setOosBack(frame, OosBack){
    if(frame === '' || frame === '-'){
        return;
    }
    if(frame in rows){
        rows[frame].OosBack += OosBack + " ";
    }else{
        if(frame <= 0 ){
            rows[0].OosBack += OosBack + " ";
        }
        if(frame >= rows.length){
            rows[rows.length-1].OosBack += OosBack + " ";
        }
    }
}
function _setMove(frame, _Move){
    if(frame === '' || frame === '-'){
        return;
    }
    if(frame in rows){
        rows[frame].Move += _Move + " ";
    }else{
        if(frame <= 0 ){
            rows[0].Move += _Move + " ";
        }
        if(frame >= rows.length){
            rows[rows.length-1].Move += _Move + " ";
        }
    }
}

const initialFramedata = (frames) =>{
    for(let i=0;i<frames;i++){
      rows[i]= createData('', i, '','');
    }
    rows[0]['ShieldsLagframes'] += '以下';
    rows[rows.length-1]['ShieldsLagframes'] += '以上';
}
const setFramedata = (punishment, attacker, defencer, frames) =>{
    initialFramedata(frames);

    if(attacker in punishment.ShieldsLag){
        const attackerFrame = punishment.ShieldsLag[attacker].ShieldsLag;
        console.log('attacker', attacker,attackerFrame);
        Object.keys(attackerFrame).forEach(key=>
            _setMove(attackerFrame[key],key)
            );
    }

    if(defencer in punishment.OutOfShieldStartup){
        const defencerFrame = punishment.OutOfShieldStartup[defencer].OosStartup;
        console.log('defencer', defencer,defencerFrame);
        Object.keys(defencerFrame).forEach(key=>
            {   _setOosBack(defencerFrame[key].back,key);
                _setOosForward(defencerFrame[key].forward,key);
            });
    }
    return rows;
}

export const createPunishment = (punishment, attacker, defencer, frames) => {
    console.log('createPunishment', punishment, attacker, defencer, frames); 
    return (('OutOfShieldStartup' in punishment) && ('ShieldsLag' in punishment))?(
            setFramedata(punishment, attacker, defencer, frames)
        ):(
            []
        );
}